.container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  background-color: #f2faf2;
}

.flex-container {
  margin: 2vw auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Courier New", Courier, monospace;
  width: 30vw;
}

.flex-container > h1 {
  font-family: "Courier New", Courier, monospace;
  font-size: 3rem;
  color: black;
  margin-bottom: 1vh;
}

.flex-container > p {
  font-family: "Courier New", Courier, monospace;
  margin-bottom: 5vh;

  font-size: 1rem;
}

.flex-container h3 {
  font-family: "Courier New", Courier, monospace;
  font-weight: 100;
  font-size: 1rem;
}

.total {
  font-weight: bolder !important;
}

.info {
  display: grid;
  grid-template-columns: repeat(1, auto);
  grid-gap: 1vw;
  list-style: none;
  text-align: start;
  width: 18vw;
  justify-content: space-around;
  margin-bottom: 1vh;
}

.info label {
  font-family: "Courier New", Courier, monospace;
}

.info input {
  font-family: "Courier New", Courier, monospace;
  width: 25vh;
  height: 4vh;
  text-align: start;
  font-size: 1.2rem;
}

.summary {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-gap: 1vw;
  list-style: none;
  text-align: start;
  width: 10vw;
  justify-content: space-between;
  margin-bottom: 1vh;
}

.summary h3 {
  margin-bottom: -1rem;
}

.container button {
  margin-left: 1vw;
  color: black;
  border-color: red;
  margin-top: 1vw;
}

.container button:hover {
  opacity: 70%;
  background-color: red;
  color: #fff;
}

.error {
  color: red;
  margin-bottom: 1rem;
  /* border: 1px solid blue; */
  /* padding-left: 1rem; */
  width: 100%;
  /* margin-left: 4.5rem; */
  font-size: .8rem;
}
